import Snap from 'snapsvg';

let disabled = false;

//    SVG  
function redrawSVG() {
  if (disabled) return;

  disabled = true;

  let screenWidth = window.innerWidth;
  let svgWidth, svgHeight, svgViewBox, pathD, patStroke;

  //  ,   path   
  if (screenWidth <= 1169) {
    //     1170  ()
    svgWidth = '434';
    svgHeight = '116';
    svgViewBox = '0 0 434 116';
    patStroke = 'url(#paint_linear_mobile)';
    pathD =
      'M2 2L39.0682 34.2892C58.1305 53.122 82.2232 51.8081 100.491 30.7855C124.054 3.63126 156.09 9.32488 175.682 43.9246L198.451 83.78C209.305 103.051 224.661 114 240.811 114H264.639C280.789 114 296.145 103.051 307.265 83.342L323.15 54.8739C342.742 19.8362 375.307 15.8944 397.546 45.6765C414.755 68.451 432 64.8512 432 64.8512';
  } else if (screenWidth >= 1170) {
    svgWidth = '1636';
    svgHeight = '175';
    svgViewBox = '0 0 1636 175';
    patStroke = 'url(#paint_linear_desktop)';
    pathD =
      'M2 2.5L142.668 51.5623C215.007 80.1779 306.436 78.1815 375.761 46.2384C465.179 4.97864 586.749 13.6299 661.097 66.2028L747.502 126.762C788.695 156.043 846.968 172.68 908.255 172.68H998.679C1059.97 172.68 1118.24 156.043 1160.44 126.096L1220.72 82.8398C1295.07 29.6014 1418.65 23.6121 1503.04 68.8647C1568.35 103.47 1633.79 98 1633.79 98';
  }

  //   SVG
  let s = Snap('#svg-container');
  s.attr({
    width: svgWidth,
    height: svgHeight,
    viewBox: svgViewBox,
  });

  //   path
  let path = s.select('#svg_path');
  path.attr({
    d: pathD,
    stroke: patStroke,
  });

  //    path
  let pathLength = Snap.path.getTotalLength(path);

  //  path  6 
  let segmentLengths = [
    pathLength * 0.165,
    pathLength * 0.17,
    pathLength * 0.165,
    pathLength * 0.165,
    pathLength * 0.17,
    pathLength * 0.165,
  ];

  //      
  let segmentStartsAndEnds = [];
  let segmentStart = 0;
  for (let i = 0; i < segmentLengths.length; i += 1) {
    let segmentEnd = segmentStart + segmentLengths[i];
    segmentStartsAndEnds.push([segmentStart, segmentEnd]);
    segmentStart = segmentEnd;
  }

  //   path   
  for (let i = 0; i < segmentStartsAndEnds.length; i += 1) {
    //   path  ,    
    let segmentPath = s.select(`#svg_${i + 1}`);
    //   path  ,   
    if (segmentPath) {
      segmentPath.remove();
    }
    //    path   
    let segment = Snap.path.getSubpath(
      path,
      segmentStartsAndEnds[i][0],
      segmentStartsAndEnds[i][1]
    );
    segmentPath = s.path(segment);
    //    
    segmentPath.attr({
      id: `svg_${i + 1}`,
      fill: 'none',
    });
  }

  setTimeout(() => {
    disabled = false;
  }, 500);
}

//     SVG   
redrawSVG();

//    resize   SVG    
window.addEventListener('resize', redrawSVG);
